<template>
  <div class="here">
    <h3>Q&A 목록</h3>
    검색하기 : <input v-model="keyword" type="text" />
    <b-button @click="search">검색</b-button>
    <table border="2">
      <tr>
        <th>번호</th>
        <th>제목</th>
        <th>작성자</th>
        <th>날짜</th>
      </tr>
      <tr v-for="q in qnas" :key="q.qnum">
        <td>{{ q.qnum }}</td>
        <td>
          <router-link :to="'/qna/read/' + q.qnum">{{ q.qtitle }}</router-link>
        </td>
        <!-- read 할 때? -->
        <td>{{ q.qwriter }}</td>
        <td>{{ q.qwritedate }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  created() {
    axios
      .get('http://localhost:8080/happyhouse/qna/list')
      .then((res) => {
        // console.log(res);
        this.qnas = res.data;
      })
      .catch((err) => console.log(err));
  },
  data() {
    return {
      keyword: '',
      qnas: [],
    };
  },
  methods: {
    search() {
      axios
        .get(`http://localhost:8080/happyhouse/qna/search/${this.keyword}`)
        .then((res) => {
          console.log(res.data);
          this.qnas = res.data;
          // this.$router.push('/qna/list');
        })
        .catch((err) => console.log(err));
    },
  },
};
</script>

<style>
th {
  background-color: cadetblue;
}
table {
  width: 60%;
  height: 100%;
  margin: auto;
  text-align: center;
}
</style>
