import Vue from "vue";
import VueRouter from "vue-router";
// import Home from "../views/.vue";
import Qna from "@/views/Qna.vue";
import QnaList from "@/components/QnaList.vue";
import QnaAdd from "@/components/QnaAdd.vue";
import QnaRead from "@/components/QnaRead.vue";
import QnaModify from "@/components/QnaModify.vue";

// import { BootstrapVue, IconsPlugin } from 'bootstrap-vue';

Vue.use(VueRouter);

const routes = [
  // {
  //   path: "/",
  //   redirect: '/qna',
  // },
  { 
    path: "/qna",
    name: "Qna",
    component: Qna,
    redirect: "/qna/list",
    children: [
      {
        path: 'list',
        name: 'QnaList',
        component: QnaList
      },
      {
        path: 'add',
        name: 'QnaAdd',
        component: QnaAdd
      },
      {
        path: 'read/:qnum',
        name: 'QnaRead',
        component: QnaRead
      },
      {
        path: 'modify/:qnum',
        name: 'QnaModify',
        component: QnaModify
      },
    ]
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;

