<template>
  <div>
    <h3>[ Q&A 상세보기 ]</h3>
    <table border="2">
      <tr>
        <th>글번호</th>
        <td>{{ qna.qnum }}</td>
      </tr>
      <tr>
        <th>글제목</th>
        <td>{{ qna.qtitle }}</td>
      </tr>
      <tr>
        <th>작성자</th>
        <td>{{ qna.qwriter }}</td>
      </tr>
      <tr>
        <th>작성일</th>
        <td>{{ qna.qwritedate }}</td>
      </tr>
      <tr>
        <th>내용</th>
        <td>{{ qna.qcontent }}</td>
      </tr>
    </table>

    <b-button squared variant="outline-secondary"><a href="#" @click="modify"> 수정 </a></b-button>
    <b-button squared variant="outline-secondary"><a href="#" @click="del"> 삭제 </a></b-button>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      qnum: 0,
      qna: {},
    };
  },
  created() {
    this.qnum = this.$route.params.qnum;
    console.log(this.qnum);
    axios
      .get(`http://localhost:8080/happyhouse/qna/detail/${this.qnum}`) // http://localhost:8080/happyhouse/qna/detail/읽으려는번호
      .then((res) => {
        console.log(res.data);
        this.qna = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    modify() {
      this.$router.push('/qna/modify/' + this.qnum);
    },
    del() {
      this.qnum = this.$route.params.qnum;
      axios
        .get(`http://localhost:8080/happyhouse/qna/delete/${this.qnum}`) // http://localhost:8080/happyhouse/qna/detail/읽으려는번호
        .then((res) => {
          console.log(res.data);
          this.$router.push('/qna/list');
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style>
tr {
  height: 50px;
}
</style>
