<template>
  <div>
    <h3>Q&A 등록</h3>
    <table border="2">
      <tr>
        <th>제목</th>
        <br />
        <td><input type="text" name="qtitle" v-model="qna.qtitle" required /></td>
        <br />
      </tr>
      <tr>
        <th>내용</th>
        <br />
        <td><textarea rows="20" cols="40" type="text" name="qcontent" v-model="qna.qcontent" /></td>
        <br />
      </tr>
    </table>

    <b-button squared variant="outline-secondary"><a href="#" @click="send">등록</a></b-button>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      qna: {
        qtitle: '',
        qcontent: '',
      },
    };
  },
  methods: {
    send() {
      axios
        .post('http://localhost:8080/happyhouse/qna/insert', this.qna)
        .then((res) => {
          console.log(res.data);
          this.$router.push('/qna/list');
        })
        .catch((ex) => console.log(ex));
    },
  },
};
</script>

<style>
input {
  border: floralwhite;
}
textarea {
  border: floralwhite;
}
</style>
