<template>
  <div>
    <h3>[ 게시글 수정 ]</h3>
    <table border="2">
      <tr>
        <th>제목</th>
        <br />
        <td><input cols="20" type="text" name="qtitle" v-model="qna.qtitle" required /></td>
        <br />
      </tr>
      <tr>
        <th>내용</th>
        <br />
        <td><textarea rows="20" cols="40" type="text" name="qcontent" v-model="qna.qcontent" /></td>
        <br />
      </tr>
    </table>

    <b-button squared variant="outline-secondary"><a href="#" @click="update">완료</a></b-button>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      qnum: 0,
      qna: {},
    };
  },
  methods: {
    update() {
      this.qnum = this.$route.params.qnum;
      console.log(this.qna);
      axios
        .post('http://localhost:8080/happyhouse/qna/update', this.qna)
        .then((res) => {
          console.log(res.data);
          this.qna = res.data;
          this.$router.push('/qna/list');
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    this.qnum = this.$route.params.qnum;
    axios
      .get(`http://localhost:8080/happyhouse/qna/detail/${this.qnum}`) // http://localhost:8080/happyhouse/qna/detail/읽으려는번호
      .then((res) => {
        console.log(res.data);
        this.qna = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>

<style></style>
